body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
  line-height: 1em; }

header {
  padding: 10px;
  background: #f1f1f1;
  border-bottom: 1px solid #EAEAEA;
  color: #3c4146;
  font-weight: bold; }

.profile-section {
  float: right; }
  .profile-section img {
    width: 30px;
    border-radius: 50%;
    position: relative;
    top: -7px; }
  .profile-section .user-name {
    font-size: 12px;
    color: #3c4146;
    margin-left: 10px;
    vertical-align: top;
    font-weight: normal; }
    .profile-section .user-name span {
      float: right;
      margin-left: 10px;
      color: crimson;
      cursor: pointer; }

.user-details {
  margin: 10px; }
  .user-details img.avatar-pic {
    width: 100px; }
  .user-details .user-bio {
    font-size: 12px;
    padding: 10px;
    border: 1px solid #eaeaea;
    border-top: none;
    line-height: 1.3em; }
  .user-details table {
    border-collapse: collapse;
    font-size: 12px;
    width: 100%; }
    .user-details table tbody tr th {
      text-align: left;
      padding: 5px 10px;
      border: 1px solid #eaeaea;
      background: #f1f1f1; }
    .user-details table tbody tr td {
      padding: 5px 10px;
      border: 1px solid #eaeaea;
      line-height: 1.4em; }

.login-form {
  border: 1px solid #eaeaea;
  padding: 10px;
  width: 500px;
  margin: 10px; }
  .login-form .err-message {
    background: burlywood;
    color: #3c4146;
    border: 1px dotted red;
    font-size: 10px;
    padding: 10px;
    margin-bottom: 10px; }
  .login-form form input {
    width: 200px;
    padding: 5px;
    border: 1px solid #eaeaea;
    outline: none;
    border-radius: 2px; }
  .login-form form button.btn-login {
    border: none;
    background: #3c4146;
    color: aliceblue;
    font-size: 12px;
    font-weight: bold;
    padding: 6px 8px;
    margin-left: 10px;
    border-radius: 2px; }

